    width: 320px;
    margin: 0;
    padding: 0;
    display: flex;
    margin-bottom: 20px;
    flex-direction: column;
    gap: 20px;
}

.input {
    border-radius: 5px;
    margin-left: 10px;
    margin-top: 1opx;
    width: 350px;
